import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { AlbergoMaster } from 'src/app/shared/models/bo.model';
import {
    BookingSolutionPaxRQ,
    BookingSolutionRoomRQ,
    DialogOptions,
    DmsSearchEvent,
    GetImmaginiPortaliServiziXAlbergoMasterRQ,
    GetImmaginiPortaliServiziXAlbergoMasterRS,
    ImmaginiXPortale,
    PaginaAlbergoMaster,
    ServizioAlbergoMaster,
} from 'src/app/shared/models/dto';
import { Header } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { CssLoaderService } from 'src/app/shared/services/css-loader.service';
import { DmsService } from 'src/app/shared/services/dms.service';
import { HeaderService } from 'src/app/shared/services/header.service';

import { PaginaDmsModalComponent } from './pagina-dms-modal/pagina-dms-modal.component';

@Component({
  selector: 'app-hotel-list',
  templateUrl: './hotel-list.component.html',
  styleUrls: ['./hotel-list.component.scss']
})
export class HotelListComponent extends BaseComponent implements OnInit, OnDestroy {

  public albergo: AlbergoMaster;
  public immagini: string[] = [];
  public portali: ImmaginiXPortale[] = [];
  public portaliVisible: ImmaginiXPortale[] = [];
  public servizi: ServizioAlbergoMaster[] = [];
  public sub: Subscription;
  public sub1: Subscription;
  public oidAlbergoMaster: number;
  public initialized: boolean = false;
  public isRichiediPax: boolean;
  public rq = new GetImmaginiPortaliServiziXAlbergoMasterRQ();
  public adultiDefault: number = 2;
  public adultiMax: number = 6;
  public bambiniMax: number = 6;
  public camerePrenotabili: number = 10;
  public etaMinimaBambini: number = 0;
  public etaMassimaBambini: number = 17;
  public htmlPagina: string;

  @ViewChild('portaliContainer') portaliContainer: ElementRef;

  constructor(private apiService: ApiService,
    private route: ActivatedRoute,
    private router: Router,
    private dmsService: DmsService,
    public containerService: ContainerService,
    private cssLoaderService: CssLoaderService,
    private headerService: HeaderService) {
    super();
  }

  ngOnInit() {
    this.containerService.setScrollingContainer('dms');

    this.dmsService.showPagine();

    this.sub = this.route.queryParamMap.subscribe(x => {
      this.initialized = false;

      let oidAlbergoMaster = x.get('oidAlbergo');

      this.isRichiediPax = x.get('isRichiediPax') == 'true';

      if (!!oidAlbergoMaster && !isNaN(+oidAlbergoMaster)) {
        this.setIsLoading(true);

        this.oidAlbergoMaster = +oidAlbergoMaster;

        this.rq.oidAlbergo = this.oidAlbergoMaster;

        let arrivoUrl = x.get('arrivo');
        let partenzaUrl = x.get('partenza');

        if (!!arrivoUrl && !!partenzaUrl) {
          try {
            let arrivo = moment(arrivoUrl, 'YYYY-MM-DD', true);
            let partenza = moment(partenzaUrl, 'YYYY-MM-DD', true);

            if (arrivo.isValid() && partenza.isValid()) {
              if (arrivo >= moment().startOf('day') && partenza > arrivo) {
                this.rq.arrivo = arrivoUrl;
                this.rq.partenza = partenzaUrl;
              }
            }
          } catch (e) { }
        }

        if (!this.rq.arrivo) {
          this.rq.arrivo = this.getDate(this.today);
        }

        if (!this.rq.partenza) {
          this.rq.partenza = this.getDate(this.tomorrow);
        }

        if (this.isRichiediPax) {
          let camereUrl = x.get('camere');

          if (!!camereUrl) {
            try {
              let camere = JSON.parse(camereUrl);

              if (camere != null && camere.length > 0) {
                this.rq.camere = [];

                if (this.camerePrenotabili > 0) {
                  camere = camere.slice(0, this.camerePrenotabili);
                }

                for (let camera of camere) {
                  if (camera.adulti > 0) {
                    camera.adulti = Math.min(camera.adulti, this.adultiMax > 0 ? Math.min(this.adultiMax, 12) : 12);

                    let room = new BookingSolutionRoomRQ(camera.adulti);

                    if (camera.bambini != null && camera.bambini.length > 0) {
                      let n = 0;

                      for (let bambino of camera.bambini) {
                        if (bambino.quantita > 0 && bambino.eta >= 0) {
                          for (let i = 0; i < bambino.quantita; i++) {
                            if ((this.bambiniMax > 0 && n + 1 > this.bambiniMax) || n + 1 + camera.adulti > 12) {
                              break;
                            } else {
                              let b = new BookingSolutionPaxRQ();
                              b.eta = Math.max(bambino.eta, this.etaMinimaBambini);
                              b.eta = Math.min(bambino.eta, this.etaMassimaBambini);
                              b.quantita = 1;
                              room.pax.push(b);
                              n++;
                            }
                          }
                        }
                      }
                    }

                    this.rq.camere.push(room);
                  }
                }
              }
            } catch (e) { }
          }

          if (this.rq.camere == null || this.rq.camere.length == 0) {
            this.rq.camere = [new BookingSolutionRoomRQ(this.adultiDefault)];
          }
        }

        this.dmsService.setLingua(x.get('lingua')).then(() => {
          this.rq.lingua = this.dmsService.lingua;
          this.download();
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });

    this.sub1 = this.dmsService.pageClicked.subscribe((x: PaginaAlbergoMaster) => {
      if (x != null) {
        if (!x.isModale) {
          if (!!x.testo) {
            this.htmlPagina = x.testo;
            this.portaliContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
          } else if (!!x.url) {
            window.open(x.url);
          }
        } else {
          let dialogOptions = new DialogOptions();
          dialogOptions.width = "80vw";
          dialogOptions.height = "80vh";
          dialogOptions.panelClass = 'no-padding-overflow-dialog';

          let ref = this.showModalDialog(PaginaDmsModalComponent, dialogOptions);
          let component = <PaginaDmsModalComponent>ref.componentInstance;

          component.testo = x.testo;
          component.url = x.url;
        }
      } else {
        this.htmlPagina = null;
        this.portaliContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
      }
    });
  }

  download() {
    this.setIsLoading(true);

    this.apiService.getPortaliImmaginiServiziAlbergoMaster(this.rq).subscribe((res: GetImmaginiPortaliServiziXAlbergoMasterRS) => {
      if (res.data != null && res.data.albergo != null) {
        this.dmsService.albergo = res.data.albergo;
        this.albergo = res.data.albergo;
        this.immagini = res.data.immagini;
        this.portali = res.data.portali;
        this.portaliVisible = this.portali.slice(0, 10);
        this.servizi = res.data.servizi;
        this.dmsService.pagine = res.data.pagine;
        this.cssLoaderService.setDmsColor(this.albergo.baseColor);
        this.cssLoaderService.loadDms(this.albergo.stileCss);
        this.headerService.showHeader(Header.Dms);
        this.initialized = true;
        this.setIsLoading(false);
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  onSearch(event: DmsSearchEvent) {
    this.rq.arrivo = event.arrivo;
    this.rq.partenza = event.partenza;
    this.rq.camere = event.camere;
    this.portali = event.portali;
    this.portaliVisible = this.portali.slice(0, 10);
    this.portaliContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
  }

  showMore() {
    this.portaliVisible = this.portali.slice(0, this.portaliVisible.length + 10);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();
  }
}