import { DOCUMENT } from '@angular/common';
import { Inject, Injectable } from '@angular/core';
import * as Color from 'color';

import { UbicazioneIstatCss } from '../models/bo.model';
import { Filter, WebApiRequestFilter, WebApiResponseFilter } from '../models/dto';
import { CriteriaOperator, FilterValueType, HtmlElement, Pagina } from '../models/enum';
import { ApiService } from './api.service';
import { UtilityService } from './utility.service';

@Injectable({
  providedIn: 'root'
})
export class CssLoaderService {

  constructor(@Inject(DOCUMENT) private document: Document,
    private apiService: ApiService,
    private utility: UtilityService) { }

  getUbicazioniIstatCss(oidAlbergo: number, pagina: Pagina, oidUbicazioneIstat: number): Promise<UbicazioneIstatCss[]> {
    return new Promise<UbicazioneIstatCss[]>(resolve => {
      let rq = WebApiRequestFilter.fromFilter('pagina', CriteriaOperator.Equal, pagina.toString(), FilterValueType.Number);

      if (oidUbicazioneIstat > 0) {
        rq.filters.push(new Filter('ubicazioneIstat.oid', CriteriaOperator.Equal, oidUbicazioneIstat.toString(), FilterValueType.Number));
      }

      this.apiService.getUbicazioniIstatCssFilter(rq, oidAlbergo).subscribe((res: WebApiResponseFilter) => {
        if (res.data.length > 0) {
          resolve(res.data);
        } else {
          this.apiService.getUbicazioniIstatCssFilter(WebApiRequestFilter.fromFilters([new Filter('pagina', CriteriaOperator.Equal, pagina.toString(), FilterValueType.Number), new Filter('ubicazioneIstat', CriteriaOperator.IsNull)]), oidAlbergo).subscribe((rs: WebApiResponseFilter) => {
            resolve(rs.data);
          });
        }
      });
    });
  }

  load(oidAlbergo: number, pagina: Pagina, oidUbicazioneIstat: number) {
    if (oidAlbergo != null) {
      this.getUbicazioniIstatCss(oidAlbergo, pagina, oidUbicazioneIstat).then((ubicazioniIstatCss: UbicazioneIstatCss[]) => {
        this.loadCss(this.generateCss(ubicazioniIstatCss), this.utility.getEnumKey(Pagina, pagina));
      });
    }
  }

  loadDms(css: string) {
    if (!!css) {
      this.loadCss(this.generateCssDms(css), 'dms');
    }
  }

  loadCss(css: string, pagina: string) {
    const id = 'custom-css-' + pagina;
    const head = this.document.getElementsByTagName('head')[0];

    let customTheme = this.document.getElementById(id) as HTMLStyleElement;

    if (customTheme) {
      customTheme.innerText = css;
    } else {
      const style = this.document.createElement('style');
      style.id = id;
      style.innerText = css;
      head.appendChild(style);
    }
  }

  unloadCss(pagina: Pagina) {
    let customTheme = this.document.getElementById('custom-css-' + this.utility.getEnumKey(Pagina, pagina)) as HTMLStyleElement;

    if (customTheme) {
      customTheme.remove();
    }
  }

  generateCss(cssStyles: UbicazioneIstatCss[]): string {
    let cssStyle = '';

    for (let css of cssStyles) {
      if (css.cssStyle != null && css.valore == null) {
        let st = css.cssStyle.replace(/\s{2,}/g, ' ').split('}');

        for (let s of st) {
          if (s.indexOf('{') != -1) {
            cssStyle += (cssStyle[cssStyle.length - 1] == ' ' ? '' : ' ');

            if (s.includes('Â°=> ')) {
              s = s.replace('Â°=> ', '');
            } else {
              cssStyle += '#' + this.utility.getEnumKey(Pagina, css.pagina) + (s[0] == ' ' ? '' : ' ');
            }

            cssStyle += s + '} ';
          }
        }
      } else if (css.valore != null && css.cssStyle == null) {
        let property = 'color';

        if (css.htmlElement == HtmlElement.Button) {
          property = 'background-color';
        }

        cssStyle += '#' + this.utility.getEnumKey(Pagina, css.pagina) + ' ' + this.utility.getEnumKey(HtmlElement, css.htmlElement) + '{' + property + ':' + css.valore + '!important;} '
      }
    }

    return cssStyle;
  }

  generateCssDms(css: string): string {
    let cssStyle = '';

    if (css != null) {
      let st = css.replace(/\s{2,}/g, ' ').split('}');

      for (let s of st) {
        if (s.indexOf('{') != -1) {
          cssStyle += (cssStyle[cssStyle.length - 1] == ' ' ? '' : ' ');

          if (s.includes('Â°=> ')) {
            s = s.replace('Â°=> ', '');
          } else {
            cssStyle += '#dms' + (s[0] == ' ' ? '' : ' ');
          }

          cssStyle += s + '} ';
        }
      }
    }

    return cssStyle;
  }

  loadCssDms() {
    const head = this.document.getElementsByTagName('head')[0];

    let customTheme = this.document.getElementById('dms-style') as HTMLLinkElement;

    if (customTheme) {
      customTheme.href = 'assets/css/dms/styles.css';
    } else {
      const link = this.document.createElement('link');
      link.id = 'dms-style';
      link.rel = 'stylesheet';
      link.type = 'text/css';
      link.href = 'assets/css/dms/styles.css';
      head.appendChild(link);
    }
  }

  setDmsColor(color: string) {
    if (!!color) {
      document.body.style.setProperty('--dms-main-color', color);
      document.body.style.setProperty('--dms-opposite-color', this.utility.invertColor(color));
      document.body.style.setProperty('--dms-transparent-color', color + '80');
    }
  }

  setColor(primaryColor: string, secondaryColor?: string) {
    if (!!primaryColor) {
      document.body.style.setProperty('--main-color', primaryColor);

      if (!secondaryColor) {
        secondaryColor = Color(primaryColor).lighten(0.5).hex();
      }

      document.body.style.setProperty('--light-color', secondaryColor);
      document.body.style.setProperty('--transparent-color', primaryColor + '80');
      document.body.style.setProperty('--inverted-color', this.utility.invertColor(primaryColor));
    } else {
      document.body.style.setProperty('--main-color', '#004987');
      document.body.style.setProperty('--light-color', '#006DC9');
      document.body.style.setProperty('--transparent-color', '#00498780');
      document.body.style.setProperty('--inverted-color', 'white');
    }
  }

  unloadCssDms() {
    let customTheme = this.document.getElementById('dms-style') as HTMLStyleElement;

    if (customTheme) {
      customTheme.remove();
    }
  }
}