import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MomentModule } from 'ngx-moment';

import { SessionService } from '../core/session.service';
import { InterfacesModule } from '../interfaces/interfaces.module';
import { CssLoaderService } from '../shared/services/css-loader.service';
import { SharedModule } from '../shared/shared.module';
import { DmsRoutingModule } from './dms-routing.module';
import { HotelDetailsComponent } from './hotel-details/hotel-details.component';
import { DmsRoomSelectComponent } from './hotel-list/dms-room-select/dms-room-select.component';
import { HotelListComponent } from './hotel-list/hotel-list.component';
import { HotelSearchComponent } from './hotel-list/hotel-search/hotel-search.component';
import { HotelComponent } from './hotel-list/hotel/hotel.component';
import { PaginaDmsModalComponent } from './hotel-list/pagina-dms-modal/pagina-dms-modal.component';

@NgModule({
    declarations: [
        HotelListComponent,
        HotelSearchComponent,
        HotelComponent,
        HotelDetailsComponent,
        DmsRoomSelectComponent,
        PaginaDmsModalComponent
    ],
    imports: [
        CommonModule,
        SharedModule,
        InterfacesModule,
        MomentModule,
        DmsRoutingModule
    ]
})

export class DmsModule {
    constructor(private cssLoaderService: CssLoaderService,
        private sessionService: SessionService) {
        this.cssLoaderService.loadCssDms();
        this.sessionService.isAttachRequestHeaders = false;
    }
}