import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
    BookingSolutionRoomRQ,
    DmsSearchEvent,
    DmsSolutionRQ,
    DmsSolutionRS,
    ServizioAlbergoMaster,
} from 'src/app/shared/models/dto';
import { StelleAlbergo, TipologiaAlbergo } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-hotel-search',
  templateUrl: './hotel-search.component.html',
  styleUrls: ['./hotel-search.component.scss']
})
export class HotelSearchComponent extends BaseComponent implements OnInit {

  @Input() oidAlbergo: number;
  @Input() servizi: ServizioAlbergoMaster[] = [];
  @Input() isRichiediPax: boolean;
  @Input() arrivo: string;
  @Input() partenza: string;
  @Input() camere: BookingSolutionRoomRQ[];
  @Input() adultiDefault: number;
  @Input() adultiMax: number;
  @Input() bambiniMax: number;
  @Input() camerePrenotabili: number;
  @Input() etaMinimaBambini: number;
  @Input() etaMassimaBambini: number;

  @Output() searchEvent: EventEmitter<DmsSearchEvent> = new EventEmitter();

  public rq: DmsSolutionRQ = new DmsSolutionRQ();

  public enumTipologiaAlbergo = TipologiaAlbergo;
  public enumStelle = StelleAlbergo;

  public showAllTipologie: boolean = false;
  public showAllStelle: boolean = false;
  public showAllServizi: boolean = false;

  constructor(private apiService: ApiService) {
    super();
  }

  ngOnInit() {
    this.rq.oidAlbergoMaster = this.oidAlbergo;
    this.rq.arrivo = this.arrivo;
    this.rq.partenza = this.partenza;

    if (this.camere != null && this.camere.length > 0) {
      this.rq.camere = [];

      for (let camera of this.camere) {
        let c = new BookingSolutionRoomRQ();
        c.pax = JSON.parse(JSON.stringify(camera.pax));
        this.rq.camere.push(c);
      }
    }
  }

  onSearch() {
    this.setIsLoading(true);

    this.apiService.getDmsSolution(this.rq).subscribe((res: DmsSolutionRS) => {
      if (res.data != null && res.data.portali != null) {
        let event = new DmsSearchEvent();
        event.portali = res.data.portali;
        event.arrivo = this.rq.arrivo;
        event.partenza = this.rq.partenza;

        if (this.isRichiediPax) {
          let camere: BookingSolutionRoomRQ[] = [];

          for (let camera of this.rq.camere) {
            let c = new BookingSolutionRoomRQ();
            c.pax = JSON.parse(JSON.stringify(camera.pax));
            camere.push(c);
          }

          event.camere = camere;
        }

        this.searchEvent.emit(event);
      }

      this.setIsLoading(false);
    });
  }

  onServizioChange(oid: number) {
    let index = this.rq.servizi.findIndex(x => x == oid);

    if (index >= 0) {
      this.rq.servizi.splice(index, 1);
    } else {
      this.rq.servizi.push(oid);
    }

    //DA CONTROLLARE
    this.onSearch();
  }

  onTipologiaChange(value: number) {
    let index = this.rq.tipologie.findIndex(x => x == value);

    if (index >= 0) {
      this.rq.tipologie.splice(index, 1);
    } else {
      this.rq.tipologie.push(value);
    }

    //DA CONTROLLARE
    this.onSearch();
  }

  onStelleChange(value: number) {
    let index = this.rq.stelle.findIndex(x => x == value);

    if (index >= 0) {
      this.rq.stelle.splice(index, 1);
    } else {
      this.rq.stelle.push(value);
    }

    //DA CONTROLLARE
    this.onSearch();
  }

  onArrivoChange() {
    setTimeout(() => {
      this.rq.arrivo = this.getDate(this.rq.arrivo);

      if (moment(this.rq.arrivo) < moment().startOf('day')) {
        this.rq.arrivo = this.getDate(moment());
      }

      if (this.rq.partenza == null || moment(this.rq.arrivo) >= moment(this.rq.partenza)) {
        this.rq.partenza = this.getDate(moment(this.rq.arrivo).add(1, 'day'));
      }
    }, 200);
  }

  onPartenzaChange() {
    setTimeout(() => {
      this.rq.partenza = this.getDate(this.rq.partenza);

      if (moment(this.rq.partenza) < moment().add(1, 'day').startOf('day')) {
        this.rq.partenza = this.getDate(moment().add(1, 'day'));
      }

      if (this.rq.arrivo == null || moment(this.rq.arrivo) >= moment(this.rq.partenza)) {
        this.rq.arrivo = this.getDate(moment(this.rq.partenza).subtract(1, 'day'));
      }
    }, 200);
  }
}