import { Directive, ElementRef, HostListener, Input, Optional } from '@angular/core';
import { NgControl } from '@angular/forms';

@Directive({
  selector: '[autocompleteOff]'
})
export class AutocompleteOffDirective {

  //DA CONTROLLARE
  @HostListener('blur') onBlur() {
    if (!this.nullable) {
      if (this._el.nativeElement.type == 'number' && !this._el.nativeElement.value) {
        if (this.control != null && this.control.control != null) {
          this.control.control.setValue('0');
        }
      }
    }
  }

  @Input() nullable: boolean = false;

  constructor(private _el: ElementRef, @Optional() private control: NgControl) {
    this._el.nativeElement.setAttribute('autocomplete', 'off');
    this._el.nativeElement.setAttribute('autocorrect', 'off');
    this._el.nativeElement.setAttribute('autocapitalize', 'none');
    this._el.nativeElement.setAttribute('spellcheck', 'false');
  }
}