import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { DettagliPortaleRQ, DettagliPortaleRS, PortaleDms } from 'src/app/shared/models/dto';
import { Header, StelleAlbergo, StelleAlbergoString, TipologiaAlbergo } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { CssLoaderService } from 'src/app/shared/services/css-loader.service';
import { DmsService } from 'src/app/shared/services/dms.service';
import { HeaderService } from 'src/app/shared/services/header.service';

@Component({
  selector: 'app-hotel-details',
  templateUrl: './hotel-details.component.html',
  styleUrls: ['./hotel-details.component.scss']
})
export class HotelDetailsComponent extends BaseComponent implements OnInit, OnDestroy {

  public sub: Subscription;
  public portale: PortaleDms;
  public oidPortale: number;
  public oidAlbergoMaster: number;
  public enumTipoAlbergo = TipologiaAlbergo;
  public immagini: string[] = [];
  public descrizione: string;
  public tariffe: string[] = [];
  public servizi: string[] = [];
  public stelle: string[] = [];
  public initialized: boolean = false;
  public codiceAlbergo: number;

  constructor(private apiService: ApiService,
    private route: ActivatedRoute,
    private router: Router,
    private containerService: ContainerService,
    private dmsService: DmsService,
    private cssLoaderService: CssLoaderService,
    private headerService: HeaderService) {
    super();
  }

  ngOnInit() {
    this.containerService.setScrollingContainer('dms');

    this.dmsService.hidePagine();

    this.sub = this.route.queryParamMap.subscribe(x => {
      this.initialized = false;
      let oidAlbergoMaster = x.get('oidAlbergo');
      let oidPortale = x.get('oidPortale');

      if (!!oidPortale && !isNaN(+oidPortale)) {
        this.setIsLoading(true);

        this.dmsService.setLingua(x.get('lingua')).then(() => {
          this.download(!!oidAlbergoMaster && !isNaN(+oidAlbergoMaster) ? +oidAlbergoMaster : null, +oidPortale);
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  download(oidAlbergoMaster: number, oidPortale: number) {
    this.setIsLoading(true);

    let rq = new DettagliPortaleRQ();
    rq.oidAlbergoMaster = oidAlbergoMaster;
    rq.oidPortale = oidPortale;
    rq.lingua = this.dmsService.lingua;

    this.apiService.getDettagliPortale(rq).subscribe((res: DettagliPortaleRS) => {
      if (res.data != null && res.data.portale != null && res.data.albergoMaster != null) {
        this.oidPortale = oidPortale;
        this.oidAlbergoMaster = res.data.albergoMaster.oid;
        this.dmsService.albergo = res.data.albergoMaster;
        this.portale = res.data.portale;
        this.immagini = res.data.immagini;
        this.descrizione = res.data.descrizione;
        this.tariffe = res.data.tariffe;
        this.servizi = res.data.servizi;
        this.codiceAlbergo = res.data.codiceAlbergo;

        if (this.portale != null && this.portale.stelle != null) {
          this.stelle = StelleAlbergoString[this.getEnumKeyOriginal(StelleAlbergo, this.portale.stelle)].split('');
        } else {
          this.stelle = [];
        }

        this.cssLoaderService.setDmsColor(this.dmsService.albergo.baseColor);
        this.cssLoaderService.loadDms(this.dmsService.albergo.stileCss);
        this.headerService.showHeader(Header.Dms);
        this.initialized = true;
        this.setIsLoading(false);
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  getUrlWebBooking() {
    return window.location.origin + '/booking?oidPortale=' + this.oidPortale + '&lingua=' + this.dmsService.lingua;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}