import { Pipe, PipeTransform } from '@angular/core';

import { SortingType } from '../models/enum';
import { LocalizationService } from '../services/localization.service';

@Pipe({ name: 'selectEnum' })
export class SelectEnumPipe implements PipeTransform {

  constructor(private localizationService: LocalizationService) { }

  transform(value: any, enumName?: string, isTranslate: boolean = true, excludedValues?: number[], includedValues?: number[], ordering?: SortingType): any {
    let keys = [];

    for (let enumMember in value) {
      if (!isNaN(parseInt(enumMember, 10))) {
        let enumValue = parseInt(enumMember, 10);

        if (excludedValues && excludedValues.indexOf(enumValue) > -1) {
          continue;
        }

        if (includedValues && includedValues.indexOf(enumValue) < 0) {
          continue;
        }

        let obj = {
          key: enumValue,
          value: value[enumMember]
        }

        if (isTranslate && enumName) {
          let stringValue = value[enumMember].toString();
          let uncapitalizedStringValue = '';

          if (stringValue.length > 1) {
            uncapitalizedStringValue = (stringValue[0] == stringValue[0].toUpperCase() && stringValue[1] == stringValue[1].toUpperCase()) ? stringValue : stringValue.charAt(0).toLowerCase() + stringValue.slice(1);
          } else if (stringValue.length == 1) {
            uncapitalizedStringValue = stringValue.toLowerCase();
          }

          obj.value = 'enumerations.' + enumName + '.' + uncapitalizedStringValue;
        }

        keys.push(obj);
      }
    }

    if (ordering != null) {
      keys.sort((a, b) => {
        let aTranslated = this.localizationService.getTranslationSync(a.value);
        let bTranslated = this.localizationService.getTranslationSync(b.value);

        if (aTranslated < bTranslated) {
          if (ordering == SortingType.Asc) {
            return -1;
          } else {
            return 1;
          }
        } else if (aTranslated > bTranslated) {
          if (ordering == SortingType.Asc) {
            return 1;
          } else {
            return -1;
          }
        } else {
          return 0;
        }
      });
    }

    return keys;
  }
  
}