import { AfterViewInit, Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Subject, Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
  BambinoCameraBookingUrlParameter,
  BookingSolutionRoomRQ,
  CameraBookingUrlParameter,
  Dimension,
  PortaleDms,
} from 'src/app/shared/models/dto';
import { StelleAlbergo, StelleAlbergoString, TipologiaAlbergo } from 'src/app/shared/models/enum';
import { ContainerService } from 'src/app/shared/services/container.service';
import { DmsService } from 'src/app/shared/services/dms.service';

@Component({
  selector: 'app-hotel',
  templateUrl: './hotel.component.html',
  styleUrls: ['./hotel.component.scss']
})
export class HotelComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() portale: PortaleDms;
  @Input() oidAlbergo: number;
  @Input() immagini: string[];
  @Input() isRichiediPax: boolean;
  @Input() arrivo: string;
  @Input() partenza: string;
  @Input() camere: BookingSolutionRoomRQ[];

  public enumTipoAlbergo = TipologiaAlbergo;
  public stelle: string[];
  public hideImage: boolean = false;
  public showImageTop: boolean = false;
  public rowHeight: number = 0;
  public imageWidth: number = 0;
  public sub: Subscription;
  public updateImageSize: Subject<void> = new Subject();
  public initialized: boolean = false;

  @ViewChild('row') row: ElementRef;
  @ViewChild('imageContainer') imageContainer: ElementRef;

  constructor(private router: Router,
    private containerService: ContainerService,
    private dmsService: DmsService) {
    super();
  }

  ngOnInit() {
    if (this.portale != null && this.portale.stelle != null) {
      this.stelle = StelleAlbergoString[this.getEnumKeyOriginal(StelleAlbergo, this.portale.stelle)].split('');
    } else {
      this.stelle = [];
    }

    this.sub = this.containerService.windowResizeEvent.subscribe((d: Dimension) => {
      this.hideImage = true;

      setTimeout(() => {
        if (d.width < 992) {
          this.showImageTop = true;
        } else {
          this.showImageTop = false;
        }

        setTimeout(() => {
          this.rowHeight = Math.floor(this.row.nativeElement.offsetHeight + 1);
          this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth + 1);
          this.hideImage = false;
          this.updateImageSize.next();
        }, 200);
      }, 200);
    });
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.containerService.dimension.width < 992) {
        this.showImageTop = true;
      } else {
        this.showImageTop = false;
      }

      this.rowHeight = Math.floor(this.row.nativeElement.offsetHeight + 1);
      this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth + 1);
      this.initialized = true;
    }, 200);
  }

  prenotaClick() {
    let arrivo = this.getDate(this.arrivo);
    let partenza = this.getDate(this.partenza);

    let camere: CameraBookingUrlParameter[] = [];

    for (let camera of this.camere) {
      let c = new CameraBookingUrlParameter();
      c.adulti = camera.numeroAdulti;

      if (camera.numeroBambini > 0) {
        c.bambini = [];

        for (let bambino of camera.pax.filter(x => x.eta >= 0)) {
          let b = new BambinoCameraBookingUrlParameter();
          b.eta = bambino.eta;
          b.quantita = bambino.quantita;
          c.bambini.push(b);
        }
      }

      camere.push(c);
    }

    window.open(window.location.origin + '/booking?oidPortale=' + this.portale.oid + '&lingua=' + this.dmsService.lingua + '&arrivo=' + arrivo + '&partenza=' + partenza + '&camere=' + JSON.stringify(camere));
  }

  scopriLaStrutturaClick() {
    this.router.navigate(['dms/hotel'], { queryParams: { oidAlbergo: this.oidAlbergo, oidPortale: this.portale.oid, lingua: this.dmsService.lingua } });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.updateImageSize.complete();
  }
}