import { CurrencyPipe, DecimalPipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'eurCurrency' })
export class EurCurrencyPipe implements PipeTransform {
  
  transform(value: any, digitsInfo: string = '1.2-2', currencySymbol?: string): string {
    if (!currencySymbol) {
      return new CurrencyPipe('it').transform(value, 'EUR', 'symbol', digitsInfo || '1.2-2');
    } else {
      return new DecimalPipe('it').transform(value, digitsInfo || '1.2-2') + ' ' + currencySymbol;
    }
  }

}